	module.exports = {"data":{"markdownRemark":{"html":"<h1>ПОДРАСТИ</h1>\n<p>Ах, мне бы подрасти<br>\nНельзя болтать ногами<br>\nИ лишнего прохожим<br>\nАх, мне бы подрасти</p>\n<p>Ах, мне бы подрасти<br>\nКого бы сделать главным<br>\nСебя или гитару<br>\nАх, мне бы подрасти</p>\n<p>Ах, мне бы подрасти<br>\nИ в патоке печали<br>\nНащупать все потоки<br>\nАх, мне бы подрасти</p>\n<p>Ах, мне бы подрасти<br>\nНебом зарасти<br>\nОт сих<br>\nДо сих<br>\nИ высмеяться в стих</p>\n<p>Ах, мне бы подрасти<br>\nНырять в чужую воду<br>\nВылеченной чайкой<br>\nМне бы подрасти</p>\n<p>Ах, мне бы подрасти<br>\nКалечить только руки,<br>\nКольцевать<br>\nАх, мне бы подрасти</p>\n<p>Ах, мне бы подрасти<br>\nПод деревом, под камнем<br>\nМягким мхом уснуть<br>\nМягким мхом уснуть</p>\n<p>Ах, мне бы подрасти<br>\nДо радости, до горя<br>\nДорасти<br>\nДо верности и ревности<br>\nМости<br>\nДо тёртости и крепкости<br>\nПолзти<br>\nИ дерево засохшее<br>\nСпасти<br>\nСнести его<br>\nНа снасти<br>\nУвезти<br>\nАх мне бы подрасти</p>\n<p>Мне бы дорасти до птах</p>"}},"pathContext":{"slug":"lyrics/podrasti"}}

